/* Home Container */
body {
    font-family: garamond;
    background-image: url('../images/forest.jpg'); /* Path to forest image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; /* Prevent background image from repeating */
    height: 100vh; /* Ensure the body covers the entire viewport height */
  }
  
  h2 {
    margin-bottom: 30px;
    text-align: center;
    color: #3b6969;
  }
  h3{
    margin-bottom: 30px;
    text-align: center;
    color: #3b6969;
  }
  p
    {
        line-height: 1;
    }
  
  /* Form Group */
  .form-group {
    margin-bottom: 20px;
  }
  
  /* Input Fields */
  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="tel"],
  textarea[type="text"] {
    width: 100%;
    padding: 15px;
    padding-right: 0px;
    border: 1px solid #92afb5;
    border-radius: 10px;
    font-size: 10px;
    background-color: rgba(255, 255, 255, 0.95); /* Adjust background transparency */
    box-sizing: border-box; /* Include padding and border in element's total width and height */
  }
  
  /* Submit Button */
  button[type="submit"] {
    padding: 15px 30px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%; /* Set button width to fill container */
  }
  
  button[type="submit"]:hover {
    background-color: #388e3c;
  }
  
  /* Error Message */
  .error-message {
    color: #dc3545;
    margin-top: 10px;
    font-size: 14px;
  }
  
