  /* Top Banner */
  /* Top Banner */
  .navbar {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    
    background-color: #333;
    color: #fff;
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar-left {
    display: flex;
    align-items: center;
  }
  
  .navbar-right {
    display: flex;
    align-items: center;
    padding: 0 20px;
  }
  
  .navbar-brand {
    color: #fff;
    text-decoration: none;
    font-size: 24px;
  }
  
  .nav-link {
    color: #fff;
    text-decoration: none;
    margin-left: 20px;
  }
  
  .nav-link:hover {
    color: #ddd;
  }  
.top-banner {
    position: relative;
    width: 100%;
    height: 93vh; /* Set height to half of window height */
    overflow: hidden;
  }
  
  .image {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensure the image covers the entire container */
  }
  
  

  /* Recent Blogs Section */
  .blogs-section {
    background:#E3FEF7;
    background-size: cover;
    background-position: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  .blog-list {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  
  .blog-item {
    background-color: #135D66;
    flex-basis: 28%; /* adjust the width of each blog item */
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  .blog-item:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    transform: translateY(-5px);
    background-color: #4eb1a8;
  }
  
  .blog-item h3 {
    font-weight: bold;
    margin-top: 0;
    font-size: 24px;
    color: #fff;
    text-align: center;
  }
  .blog-item h3:focus {
    color: #000;
  }
  .blog-item p {
    float: left;
    text-align: left;
    margin-bottom: 20px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
  }
  
  .blog-item strong {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
    color: #333;
    text-align: left;
    text-transform: uppercase;
    
  }
  
  .contact-message
{
    width: 40%;
    margin: 40px auto 60px;
}
  /* Contact Us Section */
  .contact-section {
    text-align: center;
    background-color: #E3FEF7;
    position: relative;
    width: 100%;
    height: 50vh; /* Set height to half of window height */
    overflow: hidden;
  }
  
  .contact-section p {
    margin-bottom: 10px;
  }
  
  .contact-section button {
    padding: 15px 30px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-section button:hover {
    background-color: #388e3c;
  }
  
  /* Footer */
  .footer {
    background-color: #135D66;
    
    text-align: center;
    padding: 20px 0 10px;
  }
  .copyright
  {
    color: #EAF6F6;
    font-size: 0.75rem;
    padding: 10px 0px;
  }
  .footer-link{
    color: white;
    text-decoration: none;
    padding-left: 20px;
  }
  .blog-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; /* Allow the items to wrap to the next line if there isn't enough space */
}

.blog-item {
    width: 30%; /* This will allow 3 items to fit in one line */
    padding: 20px;
    background-color: #4caf50;
    border: 1px solid #000000;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    margin-bottom: 20px; 
    text-align: center;/* Add some space below each item for when they wrap to the next line */
}

.blog-item h3 {
    font-weight: bold; /* Make the title bold */
}

.blog-item p {
    font-size: 16px;
    line-height: 1.5; /* Increase line height for readability */
}
  .video {
    width: 100%; /* This will make the video take up the full width of its container */
    height: auto; /* This will maintain the aspect ratio of the video */
    display: block; /* This will remove any default margins or padding */
  }
  .video-text {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
    font-size: 5em; /* Adjust as needed */
    font-weight: bold; /* Adjust as needed */
     /* Adjust as needed */
  }
  .video-text:hover {
    animation: fade 5s infinite;
  }
  
  @keyframes fade {
    0% {opacity: 1;}
    50% {opacity: 0.5;}
    100% {opacity: 1;}
  }
  /* For tablets: */
@media screen and (max-width: 768px) {
  .blog-item {
    width: 45%; /* Adjust the width of blog items */
  }
  .contact-message {
    width: 80%; /* Adjust the width of the contact message */
  }
}

/* For mobile phones: */
@media screen and (max-width: 480px) {
  .blog-item {
    width: 100%; /* Adjust the width of blog items */
  }
  .contact-message {
    width: 100%; /* Adjust the width of the contact message */
  }
}