/* Navigation */
.navbar {
  background-color: #77B0AA;
  padding: 10px 0;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-left {
  padding-left: 20px;
}

.navbar-right {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  padding-left: 72%; /* Adjust for spacing */
}

.navbar-brand {
  color: #333;
  font-size: 25px;
  font-weight: bolder;
  text-decoration: none;
}

.nav-link {
  margin-left: 20px;
  color: #333;
  text-decoration: none;
}

/* Map Container */
.map-container {
  height: 80vh; /* Adjust as needed for viewport height */
  width: 80vw; /* Adjust as needed for viewport width */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 10px;
  padding-left: 10%; /* Adjust for spacing */
}

/* Leaflet Map */
.leaflet-container {
  width: 100%;
  height: 100%; /* Occupy full container height */
}

/* Leaflet Popup */
.leaflet-popup-content {
  font-size: 14px;
  font-weight: bold;
}

.coordinates {
  text-align: center;
  color: red;
  font-size: 20px;
  
}

/* Responsive adjustments for smaller screens */
@media (max-width: 768px) {
  .navbar-right {
    padding-left: 50%; /* Adjust for smaller screens */
  }
  .map-container {
    width: 90vw; /* Adjust for smaller screens */
  }
}