    /* Messaging Container */
    .messaging-fm {
        width: 400px; /* Set the width of the container */
  margin: 0 auto; /* Center the container horizontally */
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.9); /* Light transparency */
  margin-top: 70px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
  font-size: 1.1em;
    }
    .messaging-container .form-group input[type="text"],
.messaging-container .form-group input[type="tel"],
.messaging-container .form-group input[type="email"],
.messaging-container .form-group textarea {
  font-size: 14px;
  width: 100%;
  padding: 10px;
}

.messaging-container .form-group textarea {
  height: 100px;
}
